body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.documentsIncluded {
    display: flex;
    justify-content: space-between;
}

hr.dividerMargin {
    margin: 30px 0;
}

.FormAddContract {
    display: flex;
    flex-direction: column;
}

.FormAddContract .MuiFormControl-root {
    margin-bottom: 20px;
}

.FormAddContract .datePickers {
    display: flex;
    margin: 20px 0;
}

.formContractAddNav {
    display: flex;
    justify-content: center;
}

.ContractNavigation {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ContractNavigation button, .ContractNavigation .setStatus {
    margin: 0 20px;
}

.ContractNavigation .setStatus {
    width: 200px;
}

.UploadFileItem {
    display: flex;
    margin-bottom: 30px;
}

.UploadFileItem button {
    margin-right: 50px;
}

.UploadFiles .moreUpload {
    margin-top: 70px;
}

.UploadFileItem h2 {
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.UploadFileItem h2 button {
    margin: 0;
}
